// 
// ES6 -------class python   =
// post
import {publicing,myPOST} from '../api/api.js'
// 
import {wxloginurl} from '../api/request.js'
var {log} = console

class logins{
	// 
	constructor(wxuser) {
	    this.wxuser = wxuser
		log(wxuser)
	}
	
	// 
	async listing(){
		// code
		let userdata = await this.wxlogin()
		// 
		let usermen = await this.gologin(userdata)
		return usermen
	}
	
	// code
	wxlogin(){
		let wxuser = this.wxuser
		return new Promise((resolve,reject)=>{
			if(wxuser != undefined){
				wx.login({
					success: (res) => {
						log(res)
						let data = {
							appid:'wx618394c82c4fd2f2',
							secret:'0d625c6f84cf973c53f1dd2c24a933a5',
							nickName:wxuser.nickName,
							avatarUrl:wxuser.avatarUrl,
							code:res.code,
							// Nn8ssidpi/C/NSTxrcklYQ==
						}
						resolve(data)
					},
					fail:(err)=>{
						log(err)
						reject('code')
					}
				})
			}else{
				console.log('')
				reject('FAIL')
			}
		})		
	}
	
	// 
	gologin(userdata){
		return new Promise((resolve,reject)=>{
			myPOST(wxloginurl,userdata)
			.then((res)=>{
				log("goLogin")
				log(res)
				log(userdata)
				resolve('SUCCESS')
				uni.setStorageSync('userInfo',res)
			})
			.catch((err)=>{
				log(err)
			})
		})
	}
	
	
	
	// code
	
}

// 
module.exports = logins