//import {errdata} from 'api/errdata.js'

// GET
let listing = function(urling){
	return new Promise((resolve,reject)=>{
		uni.request({
			url:urling,
			method:'GET',
		})
		.then((res)=>{
			// console.log(res[1])
			resolve(res[1])
		})
		.catch((err)=>{
			let errs = ' '
			//errdata.errlist(errs)
			reject(err)
		})
	})
}

// POST
let publicing = function(urling,shopdata){
	return new Promise((resolve,reject)=>{
		uni.request({
			url:urling,
			method:'POST',
			data:shopdata
		})
		.then((res)=>{
			// console.log(res[1])
			resolve(res[1])
		})
		.catch((err)=>{
			let errs = ' '
			//errdata.errlist(errs)
			reject(err)
		})
	})
}

// 
// GET

let myGET = function(url,data){
	return new Promise((resolve,reject)=>{
		// start 
		wx.cloud.init()
		wx.cloud.callFunction({
		  // 
		  name: 'Test',
		  // ,viewurl
		  data: {
		    'url':url,
			'type':'GET',
			'data':data
		  },
		})
		.then(res => {
		   resolve(res.result)
		})
		.catch((err)=>{
			 reject(err)
		})
		
	})
}

